import styled from "styled-components";
import { mediaQueries } from "../components/Themes";
import { NavLink } from "react-router-dom";
import BackButtonImg from '../assets/Images/icons8-home-button-95.png';
import "@fontsource/press-start-2p";

const BackButton = styled.img.attrs({
  src: `${BackButtonImg}`,
})`
  position: fixed;
  top: 2rem;
  left: 0.6rem;
  width: 10%;
  height: auto;
  display: none;

  @media (max-width: 768px) {
    display: block;
  }
`;


const Logo = styled.h1`
  display: inline-block;
  font-size: 2em;
  color: #E0E1DD;
  font-family: "Press Start 2P";
  position: fixed;
  left: 2rem;
  top: 2rem;

  z-index: 3;

  &:before {
    content: "rakibul.";
  }

  @media (max-width: 768px) {
    top: ${(props) => (props.backButtonVisible ? '5rem' : '2rem')};
    &:before {
      content: "r\\a a\\a k\\a i\\a b\\a u\\a l\\a .";
      white-space: pre;
    }
    
  }

  ${mediaQueries(40)`
      font-size:1.5em;
      left:1rem;
  `};
`;

const LogoComponent = ({ theme, backButtonVisible }) => {
  return (
    <NavLink to="/">
      {backButtonVisible && <BackButton />}
      <Logo color={theme} backButtonVisible={backButtonVisible} />
    </NavLink>
  );
};

export default LogoComponent;
